.hot-div{
    width:100%;
    margin-bottom:1rem;
    .hot-dl{
        width:100%;
        height:2.26rem;
        padding:0.24rem 0.3rem;
        box-sizing:border-box;
        display:flex;
        margin-top:0.2rem;
        .hot-dt{
            width:1.48rem;
            height:1.82rem;
            .hot-img{
                width:1.26rem;
                height:100%;
                display:block;
            }
        }
        .hot-dd{
            width:5.44rem;
            height:1.82rem;
            position: relative;
            border-bottom:0.01rem solid #e1e1e1;
            .hot-ul{
                width:100%;
                height:100%;
                display:flex;
                flex-direction:column;
                .nm-li{
                    width:4.5rem;
                    height:0.6rem;
                    line-height:0.6rem;
                    white-space: nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    .nm{
                        height:0.48rem;
                        float:left;
                        font-size:0.32rem;
                        font-weight:bolder;
                        color:#333333;
                        line-height:0.48rem;
                        margin-right:0.25rem;
                    }
                    .icon{
                        width:1rem;;
                        height:0.3rem;
                        float:left;
                        display:flex;
                        align-items: center;
                        font-size:0.06rem;
                        margin-top:0.1rem;
                        border:0.01rem solid #649dc5;
                        border-radius:0.02rem;
                        .one{
                            display:block;
                            height:0.26rem;
                            line-height:0.26rem;
                            color:#fff;
                            background:#649dc5;
                        }
                        .two{
                            display:block;
                            height:0.26rem;
                            line-height:0.26rem;
                            color:#649dc5;
                            background:#fff;
                        }
                    }
                }
                .sc-li{
                    width:4.5rem;
                    height:0.4rem;
                    line-height:0.4rem;
                    .txt-span{
                        font-size:0.22rem;
                        color:#666666;
                        margin-right:0.12rem;
                    }
                    .pj-span{
                        font-size:0.3rem;
                        font-weight:bolder;
                        color:#eeb23d;
                    }
                }
                .xk-li{
                    width:4.5rem;
                    height:0.4rem;
                    line-height:0.4rem;
                    .xk-span{
                        font-size:0.3rem;
                        font-weight:bolder;
                        color:#eeb23d;  
                        margin-right:0.12rem;
                    }
                    .txt-span{
                        font-size:0.22rem;
                        color:#666666;
                    }
                }
                .star-li{
                    width:4.5rem;
                    height:0.56rem;
                    line-height:0.56rem;
                    font-size:0.22rem;
                    color:#666666;
                    white-space: nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                }
                .showinfo-li{
                    width:4.5rem;
                    height:0.56rem;
                    line-height:0.56rem;
                    font-size:0.22rem;
                    color:#666666;
                }
            }
        }
    }
}